.goodsList {
  border-top: 1px solid #e8e8e8;
  padding-bottom: 1rem;
  margin-top: 0.9rem;
}
.goodsList li {
  float: left;
  width: 3.26rem;
  height: 4.66rem;
  border-bottom: 1px solid #e8e8e8;
  padding: 0.24rem;
  background-color: #fff;
}
.goodsList li img {
  width: 3.28rem;
  height: 3.28rem;
}
.goodsList li h4 {
  font-weight: normal;
  font-size: 0.28rem;
  height: 0.88rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  word-break: break-all;
  line-height: 0.44rem;
}
.goodsList li b {
  line-height: 0.36rem;
  font-size: 0.28rem;
  color: #000;
}
.goodsList li .salesnum {
  margin-left: 0.5rem;
  font-size: 0.24rem;
}
.goodsList li:nth-child(odd) {
  border-right: solid 1px #e8e8e8;
  margin-left: -1px;
  float: left;
  clear: both;
}
.am-pull-to-refresh-content {
  background-color: #fff;
}
.hotGoods {
  line-height: 0.6rem;
  font-size: 0.4rem;
  padding-left: 0.5rem;
  color: #666;
}
.hotgoodslei {
  color: #F84935;
  margin-left: 0.8rem;
}
.moveIn {
  -webkit-animation: zoomIn .8s;
          animation: zoomIn .8s;
}
