.usergoodscar {
  padding-bottom: 2.4rem;
  padding-top: 0.9rem;
  background-color: #fff;
}
.usergoodscar .cartitle {
  padding: 0.32rem;
  font-size: 0.32rem;
  color: #333333;
  line-height: 0.48rem;
  background-color: #fff;
}
.usergoodscar .usergoodscontent {
  padding: 0.4rem;
  height: 2.6rem;
  border-top: 1px solid #e8e8e8;
  background-color: #fff;
  position: relative;
}
.usergoodscar .usergoodscontent .imgcontent {
  float: left;
  width: 1.84rem;
  height: 1.84rem;
  border: 1px solid #e8e8e8;
}
.usergoodscar .usergoodscontent .imgcontent img {
  width: 100%;
}
.usergoodscar .usergoodscontent .goodsdetail {
  float: left;
  width: 4.6rem;
  padding-left: 0.2rem;
}
.usergoodscar .usergoodscontent .goodsdetail p span {
  float: left;
  line-height: 0.4rem;
  width: 2.5rem;
  color: #888888;
}
.usergoodscar .usergoodscontent .goodsdetail p strong {
  float: right;
}
.usergoodscar .usergoodscontent .goodsdetail .changeGoods {
  float: right;
  margin-top: 0.3rem;
}
.usergoodscar .usergoodscontent .goodsdetail .changeGoods span {
  display: inline-block;
  width: 0.56rem;
  height: 0.56rem;
  text-align: center;
  border: 2px solid #e8e8e8;
  line-height: 0.56rem;
  color: #666;
}
.usergoodscar .usergoodscontent .goodsdetail .changeGoods p {
  display: inline-block;
  width: 0.76rem;
  height: 0.56rem;
  border: 2px solid #e8e8e8;
  margin: 0 5px;
}
.usergoodscar .usergoodscontent .goodsdetail .changeGoods p input {
  border: none;
  width: 100%;
  text-align: center;
  line-height: 0.56rem;
}
.usergoodscar .usergoodscontent .delete {
  position: absolute;
  left: 0.5rem;
  bottom: 0.3rem;
  color: #c8c8c8;
}
.usergoodscar .carTotal {
  height: 1.4rem;
  position: fixed;
  bottom: 1rem;
  left: 0;
  width: 100%;
}
.usergoodscar .carTotal .p1 {
  float: left;
  width: 70%;
  background-color: #262626;
  line-height: 1.4rem;
  font-size: 0.28rem;
  color: #fff;
  text-align: center;
}
.usergoodscar .carTotal .p2 {
  float: left;
  width: 30%;
  background-color: #8ec21f;
  line-height: 1.4rem;
  font-size: 0.28rem;
  color: #fff;
  text-align: center ;
}
.noGoods {
  line-height: 2rem;
  font-size: 0.8rem;
  text-align: center;
  color: #888;
  background-color: #fff;
  padding-top: 0.9rem;
}
