@charset "utf-8";
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
dl,
dd,
input {
  padding: 0;
  margin: 0;
}
img {
  vertical-align: top;
  border: 0;
}
a {
  text-decoration: none;
  color: #666;
}
li {
  list-style: none;
}
html {
  font-family: "\5FAE\8F6F\96C5\9ED1";
  color: #333;
  font-size: -webkit-calc(13.33333333vw);
  height: 100%;
  background-color: #fff;
}
body {
  background-color: #f2f2f2;
  font-size: .24rem;
  background-color: #fff;
}
.cl:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}
.cl {
  zoom: 1;
}
