.imgcontent {
  width: 100%;
  height: 7.5rem;
}
.imgcontent img {
  width: 100%;
}
.goodsprice {
  padding: 0.2rem;
  background-color: #fff;
}
.goodsprice span {
  font-size: 0.6rem;
  color: #ff7c7c;
}
.goodstitle {
  padding: 0.2rem;
  background-color: #fff;
  font-weight: normal;
  font-size: 0.32rem;
  color: #000000;
  line-height: 0.4rem;
}
.addGoods {
  width: 100%;
  height: 1.1rem;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #000;
}
.addGoods div {
  float: left;
  height: 1.1rem;
  width: 50%;
}
.addGoods div a {
  margin: 0;
  height: 1.1rem;
  width: 100% !important;
}
.addGoods div p {
  float: left;
  width: 50%;
  text-align: center;
  color: #fff;
  padding-top: 0.2rem;
}
.addGoods div p img {
  width: 0.42rem;
  height: 0.42rem;
}
.addGoods div p span {
  display: inline-block;
  width: 100%;
}
.addGoods div .p2 {
  position: relative;
}
.addGoods div .p2 .hot {
  width: 0.4rem;
  position: absolute;
  top: 0;
  right: 0.2rem;
  display: inline-block;
}
.detail {
  padding-top: 0.9rem;
}
.dianzan {
  font-size: 0.6rem !important;
  margin-left: 3.7rem;
}
